@import url(https://fonts.googleapis.com/css?family=Montserrat:100,200,300,regular,500,600,700,800,900,100italic,200italic,300italic,italic,500italic,600italic,700italic,800italic,900italic);

* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

input,
button,
textarea {
  font-family: "Jost", sans-serif;
  font-size: inherit;
}

button {
  cursor: pointer;
  color: inherit;
  background-color: inherit;
}

a {
  color: inherit;
}

a:link,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

body {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: Montserrat, sans-serif;
  
}

p {
  margin: 0;
  padding: 0;
}

.main-content {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  color: #fff;
  background-color: #000;
}
.card {
  min-height: 500px;
  min-width: 310px;
  background-color: #fff;
  border-radius: 25px;
}
.nike {

}
.top {
  border-radius: 25px 25px 0 0;
  position: relative;
  height: 250px;
  padding: 25px 25px 0 25px;
  background: linear-gradient(90deg, rgba(87,190,66,1) 20%, rgba(161,225,84,1) 100%);
}
.bottom {
  color: #000;
  padding: 18px 25px 0 25px;
}

.title {
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  margin-top: 15px;
  letter-spacing: 1px;
  
}
.text {
  font-size: 10px;
  font-weight: 500;
  margin-top: 7px;
  letter-spacing: .7px;
}

.text > p {
  width: 230px;
}

.cross {
  transition: right .8s .3s ease;
  position: absolute;
  top: -45px;
  right: -170px;
  width: 400px;
  height: 400px;
  transform: scaleX(-1) rotate(20deg);
}

.nw {
  transition: right .6s ease;
  right: -170% !important;
}

.nwm {
  right: -90px !important;
  transition: right .8s .2s ease !important;
}

.cross-2 {
  position: absolute;
  top: 34px;
  right: 170%;
  width: 260px;
  height: 260px;
  transform: scaleX(-1) rotate(38deg);
  transition: right .7s ease;
}

.container {
  align-items: center;
  justify-content: center;
  display: flex;
  height: 500px;
  width: 610px;
  overflow: hidden;
}

.bottom-title {
  font-size: 18px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: .3px;
}

.flex {
  display: flex;
  align-items: center;
}

.bottom-text {
  font-size: 11px;
  font-weight: 600;
  margin-top: 2px;
}

.right {
  flex: 1 1;
  display: flex;
  justify-content: end;
}

.new {
  text-align: center;
  position: relative;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
  z-index: 2;
  background-color: rgb(7 214 172);
  border-radius: 5px;
  display: flex;
  align-items: center;
  height: 20px;
  padding: 0 5px 0 5px;
  color: #fff;
}

.new:hover {
  cursor: default;
}

.sized {
  margin-top: 30px;
}

.sized > p {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 14px;
  margin-bottom: 6px;
}

.number {
  display: flex;
  gap: 25px;
  z-index: 1;
  position: relative;
}

.num {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 3px;
}

.num:hover {
  color: #fff;
  cursor: pointer;
}

.num:hover:before {
  opacity: 1;
}

.num::before {
  transition: opacity .3s ease;
  content: "";
  background-color: rgb(7 214 172);
  position: absolute;
  top: 0;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  z-index: -1;
  opacity: 0;
}

.ten {
  filter: blur(100px);
  background-color: #000;
}

.color {
  margin-top: 25px;
}

.color > p {
  font-weight: 600;
  letter-spacing: 2px;
}

.colorr {
  margin-top: 8px;
  background-color: rgb(106, 0, 148);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  box-shadow: -5px 5px 20px rgba(72, 72, 72, 0.457);
}

.color-2 {
  margin-top: 8px;
  margin-left: 12px;
  background-color: rgb(114, 243, 89);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  box-shadow: -5px 5px 20px rgba(72, 72, 72, 0.457);
}

.submit {
  margin-left: auto;
  border-radius: 5px;
  color: #fff;
  background-color: rgba(161,225,84,1);
  padding: 8px 20px 8px 20px;
  font-size: 17px;
  box-shadow: -5px 5px 15px rgba(72, 72, 72, 0.457);
}

.submit > span {
  font-weight: 700;
}

.skiped {
  border-radius: 25px 25px 0 0;
  position: relative;
  height: 250px;
  padding: 25px 25px 0 25px;
  background: linear-gradient(90deg, rgb(106, 0, 148) 12%, rgb(227, 186, 246) 100%);
}

.skiped-button {
  margin-left: auto;
  border-radius: 5px;
  color: #fff;
  background-color: rgba(161,225,84,1);
  padding: 8px 20px 8px 20px;
  font-size: 17px;
  box-shadow: -5px 5px 15px rgba(72, 72, 72, 0.457);
  background: linear-gradient(90deg, rgb(106, 0, 148) 12%, rgb(227, 186, 246) 100%);
}